import React, { useEffect, useState } from 'react';
import './App.css';
import DeviceConnection from './DeviceConnection';
import ControlPanel from './ControlPanel';
import LogWindow from './LogWindow';
import DataInput from './DataInput';
import { v4 as uuidv4 } from 'uuid';  // 고유한 ID 생성 라이브러리
import { customTheme } from './components/common/muiTheme';
import { ThemeProvider } from '@emotion/react';
import InformationButton from './components/common/information';


function App() {
  const [port, setPort] = useState(null);
  const [dataItems, setDataItems] = useState([{ id: uuidv4(), data: '', interval: 1000, description: ''  }]);
  const [logs, setLogs] = useState([]);

  const handleConnect = (connectedPort) => {
    setPort(connectedPort);
  };

  // 저장할꺼야!
  useEffect(() => {
    const savedDataItems = localStorage.getItem('dataItems');
    if(savedDataItems) {
      const parsedItems = JSON.parse(savedDataItems).map((item) => ({
        ...item,                  // 기존 데이터 복사
        id: item.id || uuidv4(),  // id가 없으면 새로 생성
      }));
      setDataItems(parsedItems);
    }
  }, []);

  useEffect(() => {
    localStorage.setItem('dataItems', JSON.stringify(dataItems));
  }, [dataItems]);

  return (
    <ThemeProvider theme={customTheme}>
      <div className='App'>
        <DeviceConnection onConnect={handleConnect} port={port} setPort={setPort} />
        <DataInput dataItems={dataItems} setDataItems={setDataItems} />
        <ControlPanel port={port} dataItems={dataItems} setDataItems={setDataItems} logs={logs} setLogs={setLogs} />
        <LogWindow logs={logs} setLogs={setLogs} />
        <InformationButton/>
      </div>  
    </ThemeProvider>
  );
}

export default App;
