import React, { useEffect, useState, useCallback, useRef } from 'react';
import ExportModal from './ExportModal';
import ImportModal from './ImportModal';
import Button from '@mui/material/Button';
import Box from '@mui/material/Box';
import { Stack, ButtonGroup } from '@mui/material';
import { customTheme } from './components/common/muiTheme';

function ControlPanel( {port, dataItems, setDataItems, logs, setLogs }) {
    const [isSending, setIsSending] = useState(false);
    // const [currentItemIndex, setCurrentItemIndex] = useState(0);
    const currentItemIndexRef = useRef(0);  // useRef로 변경 why : currentItemIndex가 변경되어도 useEffect가 재실행되지 않음
    // 타이머 ID를 저장할 Refs
    const singleTimerRef = useRef(null);
    const multipleTimerRef = useRef(null);
    
    const testMode = true;
    const [isExportModalOpen, setIsExportModalOpen] = useState(false);
    const [isImportModalOpen, setIsImportModalOpen] = useState(false);

    const hexStringtoByteArray = (hexString) => {
        const cleanHexString = hexString.replace(/\s+/g, '');   // 공백 제거
        const bytes = [];
        for(let i = 0; i < cleanHexString.length; i += 2) {
            const byte = cleanHexString.substr(i, 2);   // 2자리씩 자르기
            bytes.push(parseInt(byte, 16));             // 16진수 문자열을 10진수 정수로 변환
        }
        return new Uint8Array(bytes);   // Uint8Array로 변환
    };

    // 데이터 송신부 처리
    const sendDataItem = useCallback(async (item) => {
            console.log('sendDataItem');
            if(!port && !testMode) {
                console.log("포트가 연결안됐는디용~");
                return; // X
            }
            
            if(testMode){
                console.log(hexStringtoByteArray(item.data));
                const timestamp = new Date().toLocaleTimeString('ko-KR');
                setLogs((prevLogs) => [...prevLogs, { timestamp, message: `TX : ${item.data}`}]);
            }

            try {
                const writer = port.writeble.getWriter();
                const dataArray = hexStringtoByteArray(item.data);
                await writer.write(dataArray);  // 데이터 송신
                writer.releaseLock();           // writer 잠금 해제
                const timestamp = new Date().toLocaleTimeString('ko-KR');
                setLogs((prevLogs) => [...prevLogs, { timestamp, message: `TX : ${item.data}`}]);
            } catch (error) {
                console.error('데이터 송신 실패 ㅠ.ㅠ : ', error);
            }
        }, [port, setLogs, testMode]
    );

    // 데이터 송신 로직 - 아이템이 하나 일 때 (하나일 때 주기적으로 보내지지 않아서 추가)
    useEffect(() => {
        if(isSending && dataItems.length === 1) {
            const sendAndSchedule = async () => {
                const item = dataItems[0];
                await sendDataItem(item);
                singleTimerRef.current = setTimeout(sendAndSchedule, item.interval);
            };

            sendAndSchedule();

            return () => {
                if(singleTimerRef.current) {                // 타이머가 존재하면
                    clearTimeout(singleTimerRef.current);   // 타이머 해제
                }
            }
        }   
    }, [isSending, dataItems, sendDataItem]);

    // 데이터 송신 로직 - 아이템이 두개 이상일 때
    // useEffect(() => {
    //     console.log('useEffect');
    //     let timerId;
    //     if(isSending && dataItems.length >= 2) {
    //         const currentItem = dataItems[currentItemIndex];
    //         sendDataItem(currentItem);

    //         timerId = setTimeout(() => {
    //             setCurrentItemIndex((prevIndex) => (prevIndex + 1) % dataItems.length);
    //             console.log(currentItemIndex);
    //         }, currentItem.interval);
    //     }

    //     return () => clearTimeout(timerId);
        
    // }, [isSending, currentItemIndex, dataItems, sendDataItem]);

    useEffect(() => {
        if(isSending && dataItems.length >= 2) {
            const sendAndSchedule = async () => {
                const currentItemIndex = currentItemIndexRef.current;
                const currentItem = dataItems[currentItemIndex];
                await sendDataItem(currentItem);
                currentItemIndexRef.current = (currentItemIndex + 1) % dataItems.length;
                multipleTimerRef.current = setTimeout(sendAndSchedule, currentItem.interval);
            };

            sendAndSchedule();  // 최초 실행

            return () => {
                if(multipleTimerRef.current) {
                    clearTimeout(multipleTimerRef.current);
                }
            };
        }
    }, [isSending, dataItems, sendDataItem]);

    // 데이터 수신부 처리
    const readData = useCallback(async () => {
        if(!port && !testMode) {
            console.log("포트가 연결 안 됐는디용~");
            return;
        }
        
        // 데이터 수신
        try {
            while (port.readable) {
                const reader = port.readable.getReader();
                try {
                    while (true) {
                        const { value, done } = await reader.read();
                        if (done) {
                            // 읽기가 끝나면 루프 종료
                            break;
                        }
                        if (value) {
                            const hexValue = Array.from(value)
                                .map((byte) => byte.toString(16).padStart(2, '0'))
                                .join(' ');
                            setLogs((prevLogs) => [...prevLogs, `[${"timestamp"}] RX : ${hexValue}`]);
                        }
                    }
                } catch (error) {
                    console.error('데이터 수신 실패 ㅠ.ㅠ : ', error);
                } finally {
                    reader.releaseLock();
                }
            } 
        } catch (error) {
            console.error('스트림 접근 실패 ㅠ.ㅠ : ', error);
        }
    }, [port, setLogs, testMode]);

    useEffect(() => {
        readData();
    }, [readData]);

    // 전송 시작 핸들러
    const handleStart = () => {
        setIsSending(true);
        // setCurrentItemIndex(0);
        currentItemIndexRef.current = 0; // 전송 시작 시 인덱스 초기화
    };

    // 전송 중지 핸들러
    const handleStop = () => {
        setIsSending(false);
    };

    return (
        <Box component="section" sx={{ p:1}}>
            <ButtonGroup variant="contained" size='small' aria-label='Small button group'>
            {!isSending ? (
                <Button variant="contained" onClick={handleStart}>ㄱㄱ</Button>
            ) : (
                <Button variant="contained" color="error" onClick={handleStop}>ㄴㄴ</Button>
            )}
            {/* copy 버튼 추가 */}

            <Button variant="contained" onClick={() => {
                setIsExportModalOpen(!isExportModalOpen);
            }}>멍</Button>
            {isExportModalOpen ?
                <ExportModal 
                    dataItems={dataItems} 
                    onClose={() => setIsExportModalOpen(false)} 
                /> : null }

            {/* import 버튼 추가 */}
            <Button variant="contained" onClick={() => {
                setIsImportModalOpen(!isImportModalOpen);
            }}>냥</Button>
            {isImportModalOpen ?
                <ImportModal 
                    setDataItems={setDataItems} 
                    onClose={() => setIsImportModalOpen(false)} 
                /> : null }

            </ButtonGroup>
        </Box>
    );
}

export default ControlPanel;